.hymn-scheduler-dialog {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  animation: fadeIn 200ms ease-in forwards;
  z-index: 0;

  .dialog-content {
    height: 100%;
    background: white;

    & > div.wrapper {
      box-shadow: 0 5px 5px #00000010;
    }
  }

  .date-picker--input {
    display: flex;
    align-items: center;
    justify-content: space-around;

    .input--start {
      padding: 0 1vw 0 3vw;
      margin-right: -3vw;
    }

    .input--end {
      padding: 0 3vw 0 3vw;
      clip-path: polygon(5% 0, 100% 0, 100% 100%, 0% 100%);
    }

    .input--start, .input--end {
      display: flex;
      flex-direction: row;
      font-size: 1.1em;
      font-weight: bold;
      flex: 1;
      height: 8vh;
      align-items: center;
      justify-content: center;

      & > * {
        align-self: center;
      }

      .start--content, .end--content {
        padding: 0 0 0 1vw;
        color: #00A3EE;
      }

      .start--content {
        //-webkit-clip-path: polygon(0 0, 100% 0, 92% 100%, 0% 100%);
        //clip-path: polygon(0 0, 100% 0, 92% 100%, 0% 100%);
      }

      .end--content {
        //-webkit-clip-path: polygon(8% 0, 100% 0, 100% 100%, 0% 100%);
        //clip-path: polygon(8% 0, 100% 0, 100% 100%, 0% 100%);
      }

      &.active {
        background: #f5d908;

        .end--content {
          color: white;
          font-weight: bolder;
        }
      }
    }
  }

  .date-picker--calendar {
    display: flex;
    flex-direction: column;
  }

  .dialog-content {
    .form--title {
      height: 80px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      margin: 0 10vw;
      border-bottom: 1px solid #00000015;

      input.title {
        border: none;
        height: 40px;
        font-size: 2em;
        outline: none;
        color: #666666;
        max-width: 250px;
      }
    }


  }
}

.date-picker--next, .dialog--next__button {
  position: absolute;
  bottom: 0;
  width: 80%;
  text-align: center;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #00a3ee;
  margin: 10px 10%;
  border-radius: 10px;
  color: white;
  animation: fadeIn 500ms linear forwards;
}